const CollegeDetails: React.FC<DataProps> = ({data, pageContext}) => {
  //   console.log(data);
  // console.log(collegeData);
  const {
    allCutoff2022Json,
    allRanks2022Json,
  } = data;
  const {collegeData, allBranches} = pageContext;
  const {nodes: cutoff2022Noes} = allCutoff2022Json;
  const {nodes: allRanks2022Noes} = allRanks2022Json;
  // console.log('allRanks2022Noes : ', allRanks2022Noes);
  return (
    <Layout headerTitle={collegeData.name} shouldGoBack>
      <Container sx={{my: 4}}>
        <Box display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center">
          <Typography variant="h5" gutterBottom component="div">
            {collegeData.name} - {collegeData.code}
          </Typography>
        </Box>
        {!!collegeData?.college_type?.name && (
          <Chip label={collegeData.college_type.name}
            size="small"
            sx={{mb: 2}}/>
        )}
        <Typography variant="body1" gutterBottom component="div">
          {collegeData.address}
        </Typography>
      </Container>

      <Divider />
      <Container sx={{my: 4}}>
        <Stack direction="row" spacing={2} sx={{mb: 1}}>
          <LocalPhoneIcon color="secondary" />
          <Typography variant="body1" component="div">
            {collegeData.phone.map((item) => item.phone).join(', ') || '--'}
          </Typography>
        </Stack>
        <Stack direction="row" spacing={2} sx={{mb: 1}}>
          <MailIcon color="secondary" />
          <Typography variant="body1" component="div">
            {collegeData.email || '--'}
          </Typography>
        </Stack>
        <Stack direction="row" spacing={2} sx={{mb: 1}}>
          <ComputerIcon color="secondary" />
          <Typography variant="body1" component="div">
            {collegeData.website || '--'}
          </Typography>
        </Stack>
      </Container>

      <Divider />

      <Container sx={{my: 4}}>
        <Grid container
          direction="row"
          // justifyContent="center"
          alignItems="center">
          <Stack direction="row" spacing={2} sx={{mb: 1}}
            alignItems="center">
            <SchoolIcon color="secondary" />
            <Typography variant="h6" component="div">
              Branches
            </Typography>
          </Stack>
        </Grid>
        
        {cutoff2022Noes.map((item, index) => {

          const branchName = allBranches.find((b) => b.code === item.brc)?.name;
          return (
            <Typography variant="body1" component="div" key={item.id}
              gutterBottom>
              {index + 1}. {branchName || item.brn}
            </Typography>
          );})}
      </Container>


      <Typography sx={{mt: 4, mb: 2}} variant="h6">Cutoff 2022</Typography>

      {cutoff2022Noes?.length ? (
        <CutoffView data={cutoff2022Noes}
          allBranches={allBranches}/>
      ) : (
        <Alert icon={false} severity="warning">
  No data Available
        </Alert>
      )}

     

      <Typography sx={{mt: 4, mb: 2}} variant="h6">Ranks 2022</Typography>
      {allRanks2022Noes?.length ? (
        <CutoffView data={allRanks2022Noes} allBranches={allBranches} />
      ) : (
        <Alert icon={false} severity="warning">
  No data Available
        </Alert>
      )}
      {/* <Accordion TransitionProps={{ unmountOnExit: true }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>Cutoff 2022</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <CutoffView
            data={cutoff2022Noes}
            allBranches={allBranches}
          />
        </AccordionDetails>
      </Accordion> */}
      
      {/* <Accordion TransitionProps={{ unmountOnExit: true }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography>Ranks 2022</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <CutoffView data={allRanks2022Noes} allBranches={allBranches} />
        </AccordionDetails>
      </Accordion> */}
      
      <Container sx={{height: 100}} />
    </Layout>
  );
};

export const query = graphql`
  query ($collegeCode: Int!) {
    allCutoff2022Json(filter: { coc: { eq: $collegeCode } }) {
      nodes {
        con
        coc
        brc
        brn
        id
        OC
        BC
        BCM
        MBC
        SC
        SCA
        ST
      }
    }
    allRanks2022Json(filter: { coc: { eq: $collegeCode } }) {
      nodes {
        con
        coc
        brc
        brn
        id
        OC
        BC
        BCM
        MBC
        SC
        SCA
        ST        
      }
    }
  }
`;

export default CollegeDetails;
